// content2 카카오톡 
// 1550까지만 줄어들게
.content2{
  width: 100%;height: 1200px;
  // min-width: 1550px;
  position: relative;
  background: #ffffff;
  display: flex;
  flex-direction: row-reverse;
  opacity: 0;
  transition: all 1s linear 0s;
  &.on{
    opacity: 1;
  }

  .box_content2{
    //background: red;
    width: 45%;
    .box_in{
      position: relative;top: 50%;
      transform: translateY(-50%);
      //font-family: $h1-font;
      margin: 0 auto;
      width: 60%;
      height: 750px;
      border: 3px solid #ffe196;
      border-radius: 10px;
      color: #333333;
      h2{
        //font-family: $subtitle-font;
        color: #ffe196;
        text-indent: 5px;
        font-size: 40px;
        padding: 15px;
        font-weight: 500;
        text-shadow: 1px 1px 2px #b9b9b9;
      }

      .content2_rotate{
        &:nth-of-type(1){
         display: inline-block;
         transform: rotate(270deg);
         position: absolute;left: -75px;top: 340px;
        }
        &:nth-of-type(2){
         display: inline-block;
         transform: rotate(90deg);
         position: absolute;right: -80px;top: 120px;
        }
        &:nth-of-type(3){
         position: absolute;
         transform: rotate(270deg);
         bottom: 190px;left: -20px;
         font-size: 40px;
        }
         }

         .content2_box_in{
          // 두개
          width: 100%;
          position: absolute;bottom: 0;left: 0;
          display: flex;
          ul{
            font-size: 16px;
            margin: 0 auto;
            &:nth-of-type(1){
            padding: 5px 15px;
             width:50%;
             li{
              display: flex;
              justify-content: space-between;
              //color: #fff;
             }
            }
            &:nth-of-type(2){
              padding: 0px 15px;
              width: 50%;
            }
            li{
             // color: #fff;
            }
          }
        }

        .kakao_mokup{
          width: 100%;height: 100%;
          object-fit: contain;
          position:absolute;top:10%;left: 8%;
          //animation: contentkakao 5s linear 0s infinite;
          //transition: all 0.2s linear 0s;
    /*         &:hover{
            transform: rotateY(20deg);
          } */
        .kakao_mokup_main_mo{
          //전체적인 크기
           position: absolute;
           top:0;left:50%;
           transform: translateX(-50%);   
           width: 330px;height: 550px;
           transition: all 0.2s linear 0s;
           //background: blue;
         .kakao_fram_main_mo{
           //틀
           margin-left: 7px;margin-top:22px;
           width:75%;height: 90%;
           border-radius: 5px 5px 32px 32px;
          background: #131313;
           overflow: hidden;
        
           .kakao_backmokup_main_mo{
            //틀 안에 들어갈것
            position: relative;top: -195px;left: 5px;
            display: block;
            width: 240px;height: 900px;
            overflow: hidden;
             video{
              width: 100%;height: 100%;
              object-fit: contain;//height: 893px;width: 247px;
               }
             }
         }
         
         .kakao_mokup_img_main_mo{
           //목업
           position: absolute;left: 0;top: 0;clear: both;
           display: block;
           width: 260px;height: 530px;
           background:url(../img/netmain.png)no-repeat 0 0;
           background-size: contain;
         }
        }
        }

    }
  }


  .kakao_box_groub{
    //background: blue;
    position: relative;top: 0;left: 0;
    width: 55%;
    height: 100%;
    // 1
    .content2_box1{
      position: absolute;top: 17%;left: 10%;
      //background: greenyellow;
      //z-index: 10;
      //position: absolute;right: 5%;top: 25%;
      .letter_name{
        color: #ffbe1a;
        text-align: center;
        font-size: 85px;
        font-family: $content-main-font;
        //font-weight: bold;
        //text-shadow: 2px 2px 2px #656565;
        span{
          text-shadow: none;
          font-size: 35px;
          color: #e7dac2;
          display: block;
          text-align: left;
          line-height: 15px;
        }
      }
    }

    // 2
    .content2_box2{
      position: absolute;top: 31%;left: 10%;
      //background: pink;
      .workP{
        font-size: 25px;
        font-weight: bold;
        color: #1b1b1b;
        font-family: $subtitle-font;
        strong{
          color: #ffbe1a;
          font-weight: bold;
          //text-shadow: 1px 1px 1px #a9a9a9;
          padding: 0 3px;
        }
      }
      .workul1{
        width: 80%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: flex-start;
        padding: 20px 0;
        
        li{
          font-weight: bold;
          //font-size: 16px;
          // color: #E76623;
          // font-weight: bold;
          // line-height: 27px;
           &:nth-of-type(2){
            padding-left: 80px;
           }
           &:nth-of-type(3){
            display: flex;
            align-items: center;
            
            strong{
              font-size: 20px;
              display: flex;
              letter-spacing: 1px;
              padding: 0 20px;
              span{
                font-size: 25px;
                padding:0 5px;
              }
            }
           }
         
        }
        
      }

    }

    .borderline{
      position: absolute;top: 47%;
      display: block;
      width: 60%;height: 3px;
      background: #ffbf57af;
    }

    // 3
    .content2_box3{
      position: absolute;right: 0%;top: 20%;
      //width: 100%;height: 100%;
      //background: blue;
      // margin-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      button{
        cursor: pointer;
        color: #181818;
        background: #dadada;
        border: none;
        margin:20px 0;
        padding: 20px 40px;
        font-size: 20px;
        border-radius: 50px 50px 2px 50px;
        font-weight: bold;
        transition: all 0.2s linear 0s;
        &:hover{
          background: #ffbe1a;
          color: #ffffff;
          box-shadow: 4px 4px 2px #bf8900;
        }
      }
    }

    // 4
    .content2_box4{
      // display: flex;
      // flex-direction: column;
      // justify-content: center;
      width: 93%;
     // background: #0095DA;
      position: absolute;
      top: 50%;left:7%;
      //background: wheat;
      display: grid;
      grid-template-columns: 1fr 2fr;
      grid-template-rows:1fr 1fr 1fr;
     // font-family: $sub-font;
      div{
        width: 100%;
        //background: blue;
        display: flex;
        &:nth-of-type(1){
          grid-column: 1/-1;
          grid-row: 1/2;
          span{
            &::before{
              background: #47455411;
            }
          }
        }
        &:nth-of-type(2){
          grid-column: 1/-1;
          grid-row: 2/3;
          span{
            &::before{
              background: #4745548e;
            }
          }
        }
        &:nth-of-type(3){
          grid-column: 1/-1;
          grid-row: 3/-1;
          span{
            &::before{
              background: #474554eb;
            }
          }
        }
        span{
          text-align: center;
          display: inline-block;
          font-size: 50px;
          width: 30%;height:100%;
          //border: 1px solid #000000;
          svg{
            position: relative;
            height: 100%;
            vertical-align: middle;
          }
          &::before{
            position: absolute;
            transform: translate(-20px, 28px);
            content: "";display: inline-block;
            width: 90px;height: 90px;
            border-radius: 50%;
            //border: 1px solid #ffbe1a;
            background: #474554eb;
          }
        }
        ul{
          //border: 1px solid red;
          //background: yellow;
          float: right;
          width: 85%;
          padding: 20px 10px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          li{
            font-size: 19px;
            line-height: 34px;
            strong{
              font-size: 20px;
              color: #ffbe1a;
              font-weight: bold;
              //text-shadow: 0.5px 0.5px 1px #1b1b1b;
              padding: 0 4px;
              }
          }
         
        }    
      }
           
    }

          
  }    
  }

  @keyframes contentkakao {
    0%{transform: rotateY(0deg);}
    25%{transform: rotateY(20deg);}
    50%{transform: rotateY(0deg);}
    75%{transform: rotateY(-20deg);}
    100%{transform: rotateY(0deg);}
  }



