                &:nth-of-type(1){
                  grid-column: 1/-1;
                  grid-row: 1/2;
                  span{
                     &::before{
                    position: absolute;
                    transform: translate(-20px, -5px);
                    content: "";display: inline-block;
                    width: 90px;height: 90px;
                    border-radius: 50%;background: #173ba6;
                    
                  }
                  }
                }
                &:nth-of-type(2){
                  grid-column: 1/-1;
                  grid-row: 2/3;
                  height: 140px;
                  transform: translateY(13px);
                  span{
                     &::before{
                    position: absolute;
                    transform: translate(-20px, 24px);
                    content: "";display: inline-block;
                    width: 90px;height: 90px;
                    border-radius: 50%;background: #8497cf;
                    
                  }
                  }
                }
                &:nth-of-type(3){
                  grid-column: 1/-1;
                  grid-row: 3/-1;
                  //transform: translateY(0px);
                  span{
                     &::before{
                    position: absolute;
                    transform: translate(-20px, 24px);
                    content: "";display: inline-block;
                    width: 90px;height: 90px;
                    border-radius: 50%;background: #cbd3ea;
                    
                  }
                  }
                }
                span{
                  text-align: center;
                  display: inline-block;
                  font-size: 50px;
                  width: 30%;height:100%;
                  //border: 1px solid #000000;
                  svg{
                    position: relative;
                    height: 100%;
                    vertical-align: middle;
                  }
                 /*  &::before{
                    position: absolute;
                    transform: translate(-14px, 5px);
                    content: "";display: inline-block;
                    width: 50px;height: 50px;
                    border-radius: 50%;background: #ff1d1d31;
                    
                  } */
                }
                ul{
                  //border: 1px solid red;
                  //background: yellow;
                  float: right;
                  width: 85%;
                  padding: 20px 10px;
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  li{
                    font-size: 19px;
                    line-height: 34px;
                    strong{
                      font-size: 20px;
                      color: #173BA6;
                      font-weight: bold;
                      //text-shadow: 0.5px 0.5px 1px #1b1b1b;
                      padding: 0 4px;
                      }
                  }
                 
                }    
              }
                   
            }
        
          }     
          }    


